@media (max-width: 850px) {
	.game {
		flex-direction: column;
		gap: 0;
	}
	.header {
		font-size: 10px;
	}
	.item {
		width: 75px;
		height: 75px;
		font-size: 2em;
		box-shadow: 0 0 30px 1px green;
	}
	.container {
		gap: 20px;
	}
	.zag {
		color: white;
		font-family: sans-serif;
		font-size: 2em;
	}
	.check {
		width: 20px;
		height: 20px;
		justify-self: center;
	}
	.btn {
		font-size: 15px;
		width: auto;
		height: 30px;
	}
	.h {
		font-size: 10px;
	}
	.btn__modal {
		font-weight: 700;
		font-size: 1em;
		padding: 20px;
	}
	.text_isNext {
		font-size: 2em;
	}
	.content {
		height: auto;
	}
}
@media (min-width: 850px) {
	.game {
		gap: 0;
	}
	.header {
		font-size: 40px;
	}
	.container {
		gap: 50px;
	}
	.item {
		width: 150px;
		height: 150px;
		font-size: 5em;
		box-shadow: 0 0 30px 1px green;
	}
	.btn {
		font-size: 0.5em;
		width: 160px;
		height: 60px;
	}
	.zag {
		color: white;
		font-family: sans-serif;
		font-size: 5em;
	}
	.check {
		width: 50px;
		height: 50px;
		justify-self: center;
	}
	.btn__modal {
		font-weight: 700;
		font-size: 2em;
		padding: 20px;
	}
	.text_isNext {
		font-size: 0.7em;
	}
}

:root {
	--index: 1vh + 1vw;
}

.check {
	cursor: pointer;
}

.game {
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.content {
	width: 100%;

	display: grid;
	justify-content: center;
	align-content: center;
	text-align: center;
}